import React from "react";

function Piece (props) {
    let source;
    
    if (props.type === "wp")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/4/45/Chess_plt45.svg/800px-Chess_plt45.svg.png";
    else if (props.type ===  "wr")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/7/72/Chess_rlt45.svg/800px-Chess_rlt45.svg.png";
    else if (props.type === "wn")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/7/70/Chess_nlt45.svg/800px-Chess_nlt45.svg.png";
    else if (props.type === "wb")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/b/b1/Chess_blt45.svg/800px-Chess_blt45.svg.png";
    else if (props.type === "wq")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/1/15/Chess_qlt45.svg/800px-Chess_qlt45.svg.png";
    else if (props.type === "wk")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/4/42/Chess_klt45.svg/800px-Chess_klt45.svg.png";
    else if (props.type === "bp")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/c/c7/Chess_pdt45.svg/800px-Chess_pdt45.svg.png";
    else if (props.type === "br")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/f/ff/Chess_rdt45.svg/800px-Chess_rdt45.svg.png";
    else if (props.type === "bn")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/e/ef/Chess_ndt45.svg/800px-Chess_ndt45.svg.png";
    else if (props.type === "bb")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/9/98/Chess_bdt45.svg/800px-Chess_bdt45.svg.png";
    else if (props.type === "bq")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/4/47/Chess_qdt45.svg/800px-Chess_qdt45.svg.png";
    else if (props.type === "bk")
        source = "https://upload.wikimedia.org/wikipedia/commons/thumb/f/f0/Chess_kdt45.svg/800px-Chess_kdt45.svg.png";
    else if (props.type === "") source = "";
    else throw new Error("Piece type not recognized");
    
    return (
        <img
            id={props.type}
            src={source}
            //onDragStart={props.onDragStart}
            //onDrag={props.onDrag}
            alt=""
            className={props.selected ? "piece select" : "piece"}
            draggable="false"/>
    )
}

export default Piece;