import React from "react";
import Piece from "./Piece";

function Square (props) {
    let character = null;
    //let piece = null;
    let selection = null;

    if (props.name === "h1" && !props.flipped)
        character = <div id="characterID">h1</div>;
    else if (props.name === "h8" && props.flipped)
        character = <div id="characterID">h8</div>;
    // Numbers
    else if (props.name[0] === "h")
        character = <div id="characterID">{props.name[1]}</div>;
    // Letters not flipped
    else if(props.name[1] === "1" && !props.flipped)
        character = <div id="characterID">{props.name[0]}</div>;
    // Letters flipped
    else if(props.name[1] === "8" && props.flipped)
        character = <div id="characterID">{props.name[0]}</div>;

    if (props.selected && props.piece === "") { selection = (
        <svg width={props.size} height={props.size} className="selection" draggable="false">
            <circle 
                draggable="false"
                cx={props.size/2} 
                cy={props.size/2} 
                r={(props.size/14) | 0} 
                fill={props.color === "dark" ? "rgb(204,226,195, 0.4)" : "rgb(75,82,72, 0.3)"}/>
        </svg>);
    }

    return (
        <div 
            className={"square " + props.color}
            id={props.name}
            style={{order: props.order, width: props.size, height: props.size}}
            //onDragOver={props.onDragOver}
            //onDrop={props.onDrop}
            onMouseDown={props.onMouseDown}
            onMouseUp={props.onMouseUp}>

            {selection}
            {character}
            {props.piece !== "" && 
                <Piece 
                    type={props.piece}
                    possibleMoves={props.possibleMoves}
                    //onDragStart={props.onDragStart}
                    //onDrag={props.onDrag}
                    selected={props.selected}
                />
            }
        </div>
    );
};

export default Square;